<template>
    <div>
        <form>
            <fieldset v-if="hideSet">
                <legend>考題設定</legend>
                <div>
                    <div class="set-ui" v-if="useSingleQuestionLength > 0">
                        <label>{{ useSingleQuestionLength }} 題單選</label>
                    </div>
                    <div class="set-ui" v-if="useMutipleQuestionLength > 0">
                        <label>{{ useMutipleQuestionLength }} 題複選</label>
                    </div>
                    <div class="set-ui">
                        <label v-if="useCateList.length == 1">{{ useCateList[0].subject }}</label>
                        <label v-else>{{ useCateList.length }} 個章節</label>
                    </div>
                    <div class="set-ui">
                        <button @click="setHide(false)">顯示設定</button>
                        <button class="primary" @click="makeTest">產生考題</button>
                    </div>
                </div>
            </fieldset>
            <fieldset v-else>
                <legend>考題設定</legend>
                <div class="set-item">
                    <label>單選</label>
                    <select v-model="singleQuestionLength">
                        <option :value="0">無</option>
                        <option v-for="item in 10" :key="item" :value="item">{{ item }} 題</option>
                    </select>
                </div>
                <div class="set-item">
                    <label>複選</label>
                    <select v-model="mutipleQuestionLength">
                        <option :value="0">無</option>
                        <option v-for="item in 10" :key="item" :value="item">{{ item }} 題</option>
                    </select>
                </div>
                <div class="set-item">
                    <label>章節</label>
                    <div class="set-select">
                        <template
                            v-for="(item, key) in cateObject"
                        >
                            <span class="set-option" :key="key">
                                <input type="checkbox" :id="'cate_'+ key" v-model="item.use">
                                <label :for="'cate_'+ key">{{ item.subject }}</label>
                            </span>
                        </template>
                    </div>
                </div>
                <div class="set-item">
                    <label>功能</label>
                    <div class="set-select">
                        <button @click="setCate(true)">章節全選</button>
                        <button @click="setCate(false)">章節全不選</button>
                        <button @click="setHide(true)">隱藏設定</button>
                        <button class="primary" @click="makeTest">產生考題</button>
                    </div>
                </div>
            </fieldset>
            <fieldset>
                <legend>考題內容</legend>
                <div class="question-section">
                    <div
                        v-for="question in useTestList"
                        :key="question.sn"
                        class="question-article"
                    >
                        <div class="header">
                            <div
                                class="type"
                                :class="'is-'+ question.type"
                            >{{ typeTable[question.type] }}</div>
                            <div class="caption">{{ question.subject }}</div>
                        </div>
                        <div class="content" v-if="showAnswer">
                            <div
                                v-for="(item, index) in question.test"
                                :key="index"
                                class="question"
                            >
                                <div
                                    class="item"
                                    :class="[item.answer === item.realAnswer? 'is-true': 'is-false']"
                                >
                                    <input type="checkbox" v-model="item.answer" disabled>
                                    <label>{{ item.question }}</label>
                                </div>
                            </div>
                        </div>
                        <div class="content" v-else>
                            <div
                                v-for="(item, index) in question.test"
                                :key="index"
                                class="question"
                            >
                                <div class="item">
                                    <input type="checkbox" :id="question.sn +'_'+ index" v-model="item.answer">
                                    <label :for="question.sn +'_'+ index">{{ item.question }}</label>
                                </div>
                            </div>
                        </div>
                        <div class="extend" v-if="showAnswer && question.extend">{{ question.extend }}</div>
                    </div>
                </div>
                <div class="function-area">
                    <button v-if="showAnswer" @click="makeTest">產生考題</button>
                    <button v-else class="primary" @click="showAnswer = true">對答案</button>
                </div>
            </fieldset>
        </form>
        <!-- <h1>{{ targetData.subject }}</h1>
        <h3>{{ targetData.sn }}</h3>
        <ul :class="[showAnswer? 'show-answer': '']">
            <li v-for="n in 4" :key="n">
                <div :class="[testRsult[n - 1] == targetData['c' + n]? 'is-true': 'is-false']">
                    <input type="checkbox" :id="'checkbox_'+ n" v-model="testRsult[n - 1]">
                    <label :for="'checkbox_'+ n">{{ targetData['a'+ n] }}</label>
                </div>
            </li>
        </ul>
        <div :class="[showAnswerOthers ? 'show-others-visible':'show-others-hidden']">{{targetData.others}}</div>
        <br>
        <button @click="doShowAnswer">對答案</button>
        <button @click="setRandom">下一題</button> -->
    </div>
</template>

<script>

    export default {
        data: function(){
            let _singleQuestionLength = 3,
                _mutipleQuestionLength = 10;

            return {
                cateTable: {
                    1: '人事管理規則',
                    2: '勞動基準法及施行細則',
                    4: '工會法',
                    5: '出入廠管理規則',
                    6: '資材管理規則',
                    7: '存量管制作業辦法',
                    8: '設備保養管理規則',
                    9: '工程管理規則',
                    10: '營業管理準則',
                    11: '外銷佣金匯付管理辦法',
                    12: '外包承攬作業管理辦法',
                    13: '固定資產管理辦法',
                    14: '成本管理作業',
                    15: '核決權限管理辦法',
                    16: '職業安全衛生法',
                    17: '製程安全管理辦法',
                    18: '工作安全許可管理辦法',
                    19: '環保監督管理辦法',
                    20: 'AI人工智慧基礎',
                },
                typeTable: {
                    single: '單選',
                    mutiple: '複選'
                },
                cateObject: {},
                singleQuestionLength: _singleQuestionLength,
                mutipleQuestionLength: _mutipleQuestionLength,
                useCateObject: {},
                useSingleQuestionLength: _singleQuestionLength,
                useMutipleQuestionLength: _mutipleQuestionLength,
                hideSet: true,
                testList: [],
                useTestList: [],
                showAnswer: false,
            }
        },
        computed: {
            useCateList: function() {
                return this.getUseCateList(this.useCateObject);
            },
            // targetData: function(){
            //     let $result = {}
                
            //     let $testList = this.testList,
            //         _random = this.random;
                    
            //     let $target = $testList[_random]

            //     if( $target ) {

            //         let $key = ['sn', 'cate', 'subject', 'a1', 'a2', 'a3', 'a4', 'c1', 'c2', 'c3', 'c4','others']

            //         $key.forEach((key, index) => {
            //             $result[key] = $target[index]
            //         });
            //     }

            //     return $result;
            // }
        },
        methods: {
            // setRandom: function(){
            //     this.resetTest();

            //     let $testList = this.testList,
            //         _length = $testList.length;

            //     let _now = this.random,
            //         _result = Math.floor(Math.random() * _length);

            //     if( _now !== _result ) {
            //         this.$set(this, 'random', _result);
            //     }else {
            //         this.setRandom();
            //     }
            // },
            // doShowAnswer: function(){
            //     this.$set(this, 'showAnswer', true);
            //     this.$set(this,'showAnswerOthers',true);
            // },
            // resetTest: function(){
            //     this.$set(this, 'showAnswer', false);
            //     this.$set(this,'showAnswerOthers',false);
            //     this.$set(this, 'testRsult', [false, false, false, false]);
            // },
            copy: function(obj){
                return JSON.parse(JSON.stringify(obj));
            },
            getUseCateList: function(obj) {
                let $result = []

                for( let _key in obj ) {
                    let $item = obj[_key]

                    if( $item.use ) {
                        $result.push($item);
                    }
                }

                return $result;
            },
            setHide: function(boolean) {
                this.$set(this, 'hideSet', boolean);
            },
            makeTest: function(){
                let _singleQuestionLength = this.singleQuestionLength,
                    _mutipleQuestionLength = this.mutipleQuestionLength,
                    $cateObject = this.cateObject;

                let $alertMsg = []

                if( _singleQuestionLength + _mutipleQuestionLength === 0 ) {
                    $alertMsg.push('請至少加入一個題目');
                }

                let $useCateObject = this.getUseCateList($cateObject);

                if( $useCateObject.length === 0 ) {
                    $alertMsg.push('請至少勾選一個章節');
                }

                if( $alertMsg.length ) {
                    alert($alertMsg.join(',') +'.');
                }else {
                    this.$set(this, 'useSingleQuestionLength', _singleQuestionLength);
                    this.$set(this, 'useMutipleQuestionLength', _mutipleQuestionLength);
                    this.$set(this, 'useCateObject', JSON.parse(JSON.stringify($cateObject)));

                    this.$set(this, 'showAnswer', false);

                    this.setHide(true);

                    let $useCateList = []

                    for( let _key in $cateObject ) {
                        let $item = $cateObject[_key]

                        if( $item.use ) {
                            $useCateList.push(parseFloat(_key));
                        }
                    }

                    let $cloneList = this.copy(this.testList),
                        $testList = $cloneList.filter(item => $useCateList.includes(item.cate)),
                        $singleQuestionList = $testList.filter(item => item.type === 'single'),
                        $mutipleQuestionList = $testList.filter(item => item.type === 'mutiple');

                    let $useTestList = []

                    let $_getQuestion = (_length, $source) => {

                            if( _length ) {

                                if( _length > $source.length ) {
                                    $useTestList = $useTestList.concat($source);
                                }else {
                                    $_addQuestion(_length, $source);
                                }
                            }
                        },
                        $_addQuestion = (_length, $source) => {

                            for( let _i = 0; _i < _length; _i++ ) {
                                let _index = Math.floor(Math.random() * $source.length),
                                    $target = $source[_index]
                                
                                $useTestList.push($target);

                                $source.splice(_index, 1);
                            }
                        }

                    $_getQuestion(_singleQuestionLength, $singleQuestionList);
                    $_getQuestion(_mutipleQuestionLength, $mutipleQuestionList);

                    this.$set(this, 'useTestList', $useTestList);
                }
            },
            setCate: function(boolean){
                let $cateObject = this.cateObject;
                
                for( let _key in $cateObject ) {
                    $cateObject[_key].use = boolean;
                }
            },
            initData: function(data){
                let $testList = [],
                    $cateObject = {}

                let $_getQuestionType = (num) => {
                        
                        if( num > 1 ) {
                            return 'mutiple';
                        }else {
                            return 'single';
                        }
                    }

                data.forEach(item => {
                    let _cate = item[1];

                    if( !$cateObject[_cate] ) {

                        $cateObject[_cate] = {
                            use: true,
                            subject: this.cateTable[_cate],
                        }
                    }

                    $testList.push({
                        sn: item[0],
                        cate: _cate,
                        subject: item[2],
                        type: $_getQuestionType(item[7] + item[8] + item[9] + item[10]),
                        test: [
                            {
                                question: item[3],
                                answer: false,
                                realAnswer: !!item[7],
                            },
                            {
                                question: item[4],
                                answer: false,
                                realAnswer: !!item[8],
                            },
                            {
                                question: item[5],
                                answer: false,
                                realAnswer: !!item[9],
                            },
                            {
                                question: item[6],
                                answer: false,
                                realAnswer: !!item[10],
                            },
                        ],
                        extend: item[11],
                    });
                });

                let $useCateObject = this.copy($cateObject);

                this.$set(this, 'testList', $testList);
                this.$set(this, 'cateObject', $cateObject);
                this.$set(this, 'useCateObject', $useCateObject);

                this.makeTest();
            },
            getData: function(){
                let _url = 'https://script.google.com/macros/s/AKfycbxcFzZrlxNxOR6B5nTwVRs3SZZthzpus8L_W1CHLmqP9j6gu_NIE34gVcWdOQCDBResJA/exec';

                return this.$http.get(_url);
            },
            init: async function(){

                try {
                    let $response = await this.getData()
                    
                    this.initData($response.data);

                }catch(error) {
                    console.error(error)
                }
            }
        },
        created: function(){
            this.init();
        }
    }
</script>

